    image: '/img/blog3.jpg',
    body:"<p><h2>Sử dụng các màu an toàn</h2>Màu sắc vô cùng đa dạng, muôn hình muôn vẻ, mỗi màu lại có thể phối hợp một cách hài hòa với một hay nhiều màu sắc khác. Tuy nhiên, có một vài sắc độ được xem là “màu an toàn”. Những màu này có thể kết hợp với rất nhiều kiểu màu cũng như phong cách thiết kế khác nhau, không bị ảnh hưởng bởi các xu hướng mà cực kỳ bền vững với thời gian: Màu đen: Không cần bàn cãi, một màu trung tính mang vẻ hiện đại, lịch lãm mà có thể phối với hầu hết các gam màu. Màu trắng: Sáng sủa, thoáng đãng và trang nhã, màu trắng là lựa chọn hợp lý cho mọi phong cách thiết kế. Màu nâu: Vốn được xem là màu của giới thượng lưu, màu nâu đem lại vẻ sang trọng, giàu sang và cổ điển, mang lại vẻ đẹp vượt thời gian mà không một ai có thể phủ nhận được. Màu xám: Màu sắc đem lại cảm giác hiện đại nhiều nhất, với sự mạnh mẽ nhưng lịch lãm, thông thoáng nhưng rất ấm áp, đưa sự tiên tiến trong lối phối màu vào các thiết kế của bạn.</p> <p><h2>Tạo điểm nhấn với màu nổi bật</h2>Tất nhiên, không hề có một quy luật nào ràng buộc bạn với duy nhất một lối thiết kế dựa trên một bảng màu nhất định cả. Không gian sống là của riêng bạn, và bạn hoàn toàn có thể tô điểm nó bằng những màu sắc nổi trội, tạo điểm nhấn cho không gian thêm thú vị và bắt mắt. Hãy hình dung một căn phòng với lối phối màu hiện đại giữa các tông màu mát, được tô điểm bởi một chiếc ghế với tông màu ấm nội bật. Chắc hẳn căn phòng sẽ trở nên đa dạng hơn, tạo sự khác biệt nhưng vẫn giữ được nét hài hòa vốn có.</p>",
